<template>
    <div >


    <div class="order_detail_page" >
        <header id="head_top">
            <section class="head_goback" @click="goBack">
                <i class="el-icon-arrow-left"></i>
            </section>
            <section class="title_head ellipsis">
                <span class="title_text">
                    订单详情
                </span>
            </section>
        </header>
        <section class="scroll_container" id="scroll_section" style="overflow-y: scroll">
            <section class="scroll_insert">
                <section class="order_titel">
                    <img :src="imgUrl+orderDe.restaurant_image_url" alt="" class="restaurant_image">
                    <p>{{resOrderDetail._doc?resOrderDetail._doc.status_bar.title:''}}</p>
                    <p></p>
                    <span class="order_again" @click="goShop">再来一单</span>
                </section>
                <section class="food_list">
                    <span class="food_list_header" @click="goShop">
                        <div class="shop_name">
                              <img :src="imgUrl+orderDe.restaurant_image_url" alt="" >
                            <span>{{resOrderDetail._doc?resOrderDetail._doc.restaurant_name:''}}</span>
                        </div>
                        <i class="el-icon-arrow-right"></i>
                    </span>
                    <ul class="food_list_ul" v-if="resOrderDetail._doc.basket?resOrderDetail._doc.basket.group:''">
                        <li v-for="(v,k) in resOrderDetail._doc.basket.group" :key="v[0].name">
                            <p class="food_name ellipsis">{{v[0].name}}</p>
                            <div class="quantity_price">
                                <span>×{{v[0].quantity}}</span>
                                <span>￥{{v[0].price}}</span>
                            </div>
                        </li>
                    </ul>
                    <div class="deliver_fee">
                        <span>{{resOrderDetail._doc.basket?resOrderDetail._doc.basket.deliver_fee.name:''}}</span>
                        <span>{{resOrderDetail._doc.basket?resOrderDetail._doc.basket.deliver_fee.price:''}}</span>
                    </div>
                    <div class="packing_fee">
                        <span>{{resOrderDetail._doc.basket?resOrderDetail._doc.basket.packing_fee.name:''}}</span>
                        <span>{{resOrderDetail._doc.basket?resOrderDetail._doc.basket.packing_fee.price:''}}</span>
                    </div>
                    <div class="pay_ment">实付{{resOrderDetail._doc.basket?resOrderDetail._doc.total_amount+'.00':''}}</div>
                </section>
            </section>
            <section class="order_detail_style">
                <header>配送信息</header>
                <section class="item_style">
                    <p class="item_left">送达时间：</p>
                    <div class="item_right">
                        <p>{{resOrderDetail?resOrderDetail.deliver_time:''}}</p>
                    </div>
                </section>
                <section class="item_style">
                    <p class="item_left">送货地址：</p>
                    <div class="item_right">
                        <p>{{resOrderDetail?resOrderDetail.consignee:''}}</p>
                        <p>{{resOrderDetail?resOrderDetail.phone:''}}</p>
                        <p></p>
                    </div>
                </section>
                <section class="item_style">
                    <p class="item_left">配送方式：</p>
                    <div class="item_right">
                        <p>蜂鸟专送</p>
                    </div>
                </section>

            </section>
            <section class="order_detail_style">
               <header>订单信息</header>
                <section class="item_style">
                    <p class="item_left">订单号：</p>
                    <div class="item_right">
                        <p>{{resOrderDetail._doc?resOrderDetail._doc.order_time:''}}</p>
                    </div>
                </section>
                <section class="item_style">
                    <p class="item_left">支付方式：</p>
                    <div class="item_right">
                        <p>{{resOrderDetail.pay_method}}</p>
                    </div>
                </section>
                <section class="item_style">
                    <p class="item_left">下单时间：</p>
                    <div class="item_right">
                        <p>{{resOrderDetail._doc?resOrderDetail._doc.formatted_created_at:''}}</p>
                    </div>
                </section>
            </section>
        </section>
    </div>
    </div>
</template>

<script>
    export default {
        name: "OrderDetail",
        data(){
            return{
                user:[],
                orderDe:"",
                resOrderDetail:"",
                imgUrl:"https://elm.cangdu.org/img/",
                latitude:"",
                longitude:"",
            }
        },
        created(){
            // this.orderDe = this.$route.query.orderDetail;
            // console.log(this.orderDe)
            // this.$store.commit("orderDe",this.orderDe);
            this.orderDe = this.$store.state.orderDe;
            if(localStorage.getItem("user")){
                this.user = JSON.parse(localStorage.getItem("user"))
            }
            this.axios.get("bos/v1/users/"+this.user[0].user_id+"/orders/"+this.orderDe.unique_id+"/snapshot").then(res=>{
                this.resOrderDetail = res;
                console.log(res);

            }).catch(err=>{

            })
            this.placeHistory = JSON.parse(localStorage.getItem('placeHistory'))
            // console.log(this.placeHistory);
            this.end = this.placeHistory?this.placeHistory.slice(-1):null
            // console.log(this.end)
            //如果是未选地区
            if (this.placeHistory == null) {
                this.address ={
                    name: "上海迪士尼乐园",
                    address: "上海市浦东新区申迪西路753号",
                    latitude: 31.14419,
                    longitude: 121.66034,
                    geohash: "31.14419,121.66034"
                }
                this.latitude = this.address.latitude;
                this.longitude = this.address.longitude;
                this.geohash = this.address.geohash;

            }else {
                //选择了地点根据地点获取轮播图店铺信息
                this.geohash = this.end[0].geohash
                this.latitude = this.end[0].latitude;
                this.longitude = this.end[0].longitude;
            }
        },
        methods:{
            //头部返回上一级
            goBack()
            {
                this.$router.back(-1);
            },
            goShop(k){
                this.$router.push({
                    path:"/shop",
                    query:{
                        shopid:this.orderDe.restaurant_id,
                        latitude:this.latitude,
                        longitude:this.longitude,
                        // geohash:this.$route.query.geohash,
                    }

                })
            },
        }
    }
</script>

